
#header {
  /*height: 80px;*/
}
#header .container {
    /*height: 80px;*/
    line-height: 80px;
}
#header .container img {
      padding: 16px 0;
      float: left;
}
#header .container ul {
      float: left;
      margin-left: 46px;
      z-index: 0;
}
#header .container ul li {
      float: left;
      padding-right: 30px;
      cursor: pointer;
      position: relative;
}
#header .container ul li a:hover,
      #header .container ul li a.active {
        color: #2cbda1;
}
#header .container ul li:hover ul {
        display: block;
}
#header .container ul li ul {
        display: none;
        z-index: 5;
        position: absolute;
        margin: 0;
        left: -12px;
        top: 80px;
        line-height: 18px;
        color: black;
        width: 140px;
        background: #fff;
}
#header .container ul li ul li {
          width: 104 px;
          height: 40px;
          line-height: 40px;
          margin: 0 18px;
          border-bottom: 1px #e8e8e8 solid;
}
#header .container ul li ul li:nth-last-child(1) {
            border-bottom: none;
}
#header .container ul li ul li:hover, #header .container ul li ul li.active {
            color: #2cbda1;
}
#header .container div {
      float: right;
      height: 40px;
}
#header .container div span {
        margin-right: 24px;
}
#header .container div button {
        width: 140px;
        height: 40px;
        padding: auto;
        background: #0eb493;
        color: #fff;
        font-size: 18px;
        border-radius: 4px;
}

#footer {
  background: #f4f4f4;
  padding: 50px 0 40px 0;
}
#footer .container img {
    display: block;
    padding: 0 0 24px 24px;
}
#footer .container ul {
    line-height: 22px;
}
#footer .container ul.one {
    margin-left: 24px;
}
#footer .container ul.two {
    margin-left: 68px;
}
#footer .container div.fl {
    margin-left: 64px;
}
#footer .container div.fl p {
      padding-bottom: 12px;
}
#footer .container div.fl a {
      display: inline-block;
      width: 60px;
      height: 60px;
      margin-right: 16px;
      background-image: url(data:image/png;base64,);
}
#footer .container div.fl a:nth-child(3) {
      background-position: 0px -60px;
}
#footer .container div.fl a:nth-child(4) {
      background-position: 0px -120px;
}
#footer .container div.fr {
    margin-right: 24px;
    text-align: right;
}
#footer .container div.fr p:nth-child(1) {
      font-size: 18px;
}
#footer .container div.fr p:nth-child(2) {
      font-size: 36px;
}
#footer .container div.fr p:nth-child(3) {
      font-size: 12px;
}
