#banner {
  position: relative;
  min-height: 450px;
  background: #fff;
  /*占位符图片时为了撑开父元素的高度*/
  /*	.placeholde{visibility: hidden;z-index: 0;}*/
  color: white;
  font-size: 18px;
}
#banner img {
    width: 100%;
    display: block;
    height: auto;
}
#banner #banner1 {
    position: relative;
    z-index: 2;
    top: 0;
    text-align: center;
    background: #fff;
}
#banner #banner1 ul {
      position: absolute;
      top: 50%;
      width: 100%;
}
