    position: absolute;
    z-index: 3;
    top: 0;
    background: #fff;
}
#banner #banner2 ul {
      color: #202020;
      position: absolute;
      top: 50%;
      left: 59.58333%;
      padding-right: 2%;
}
@media screen and (max-width: 1054px) {
#banner #banner2 ul {
        position: relative;
        width: 100%;
