@charset "UTF-8";
#designs .design {
  background-color: #f8f7f7;
  text-align: center;
  padding-bottom: 90px;
  color: #5a5a5a;
}
#designs .design img {
    width: 100%;
}
#designs .design h1 {
    color: #0eb493;
    font-size: 48px;
    padding-top: 88px;
    padding-bottom: 30px;
}
#designs .design p {
    font-size: 18px;
}
#designs .design ul {
    text-align: left;
}
#designs .design ul li {
      display: inline-block;
}
#designs .design ul li:nth-child(1) {
      margin-left: 32px;
}
#designs .design ul li:nth-child(2) {
      margin-left: 86px;
}
#designs .design ul li:nth-child(3) {
      margin-left: 160px;
}
#designs .see {
  height: 500px;
  /*  display:table-cell;  
      vertical-align:middle;*/
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
#designs .see .inner {
    padding: 0px 3em 50px 32px;
    /*padding-left: 32px;
				padding-right:3em;*/
}
#designs .see .inner h1 {
      color: #0eb493;
      font-size: 48px;
}
#designs .see .inner p {
      padding-top: 30px;
}
#designs .see img {
    width: 100%;
    display: block;
    padding: auto 0;
}
@media screen and (max-width: 750px) {
#designs .see {
    display: block;
}
}
#designs .chart {
  min-height: 580px;
  background-image: url(../dist/static/img/小孩1.2226d4f.png);
  background-repeat: no-repeat;
  background-position: right top;
  background-color: #fbfaf9;
  color: #5a5a5a;
  font-size: 18px;
}
#designs .chart .container .time {
    display: inline-block;
    margin-left: 22px;
}
#designs .chart .container .time img {
      display: block;
      margin-top: 60px;
}
#designs .chart .container .inner {
    width: 520px;
    float: right;
}
#designs .chart .container .inner h1 {
      color: #0eb493;
      font-size: 48px;
      padding-top: 136px;
      padding-bottom: 30px;
}
#designs .chart .container .inner p {
      padding-bottom: 42px;
}
#designs .chart .container .inner button {
      width: 170px;
      height: 50px;
      color: #0eb493;
      font-size: 26px;
      border: 2px solid #0eb493;
      border-radius: 22px;
      margin-right: 30px;
}
#designs .chart .container .inner button:hover, #designs .chart .container .inner button.cur {
        cursor: pointer;
        color: #fff;
        background: #0eb493;
}
#designs .quote {
  height: 416px;
  background-color: #0eb493;
}
#designs .quote .container {
    height: 416px;
    position: relative;
}
#designs .quote .container div {
      width: 620px;
      color: #fff;
}
#designs .quote .container div h1 {
        color: #fff;
        font-size: 48px;
        padding-top: 80px;
        padding-bottom: 30px;
}
#designs .quote .container div p {
        padding-bottom: 42px;
}
#designs .quote .container div button {
        width: 170px;
        height: 50px;
        color: #fff;
        font-size: 26px;
        border: 2px solid #fff;
        border-radius: 22px;
        margin-right: 30px;
        background-color: #0eb493;
}
#designs .quote .container div button:hover, #designs .quote .container div button.cur {
          cursor: pointer;
          color: #0eb493;
          background: #fff;
}
#designs .quote .container img {
      position: absolute;
      bottom: 0;
      right: 84px;
}

